















import { Component, Prop, Vue } from 'vue-property-decorator'
import { eventBus } from '../../../main'
import { DisplayModule } from '../../../store/modules/display.module'
import wrap from '@vue/web-component-wrapper'

@Component
export default class SelectCity extends Vue {
  cities = [
    'Москва',
    'Калининград',
    'Екатеринбург',
    'Санкт-Петербург',
    'Пермь',
    'Киров',
    'Киев',
    'Алматы'
  ];

  selectCity (cityName: string) {
    this.$store.dispatch('display/toggleSelectCity')
  }

  get showSelectCity () {
    return DisplayModule.showBlock.selectCity
  }
}

const SelectCityComponent = wrap(Vue, SelectCity)

window.customElements.define('custom-select-city', SelectCityComponent)
